@font-face {
  font-family: Satoshi-Medium;
  src: url("Satoshi-Medium.5be63f72.woff2") format("woff2"), url("Satoshi-Medium.de33c0c3.woff") format("woff"), url("Satoshi-Medium.0cd9b7ce.ttf") format("truetype");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: Satoshi-Regular;
  src: url("Satoshi-Regular.5cd7338f.woff2") format("woff2"), url("Satoshi-Regular.23214b96.woff") format("woff"), url("Satoshi-Regular.e855a0d3.ttf") format("truetype");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

.desktop {
  display: block;
}

.mobile, .small-mobile {
  display: none;
}

.color-pink {
  color: #fdc5ac;
}

.color-green {
  color: #224135;
}

.bg-pink {
  background-color: #fdc5ac;
}

.bg-grey {
  background-color: #edeae7;
}

.bg-green {
  background-color: #224135;
}

html, body {
  background-color: #edeae7;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

p {
  font-family: Satoshi-Regular, sans-serif;
}

.content-container {
  width: 100%;
  position: relative;
}

.content-container .container {
  z-index: 3;
  height: 100%;
  position: relative;
}

.content-intro {
  height: 100vh;
}

.logo-container {
  height: 139px;
  width: auto;
  margin-top: 80px;
}

.logo-container img {
  width: auto;
  height: 100%;
}

.intro-image {
  width: 65%;
  height: calc(100vh - 80px);
  background-image: url("hero_shot.0e318c18.jpg");
  background-position: 0;
  background-size: cover;
  position: absolute;
  top: 0;
  right: 0;
}

.intro-text-container {
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
}

.intro-text-container p {
  text-transform: uppercase;
  color: #27574e;
  letter-spacing: .06em;
  font-family: Satoshi-Medium, sans-serif;
  font-size: 22px;
  line-height: 29px;
}

#intro-image {
  opacity: 0;
}

#intro-logo, #intro-text {
  opacity: 0;
  z-index: 2;
  position: relative;
  transform: translateY(10px);
}

.section-main {
  z-index: 2;
  padding: 160px 0;
  position: relative;
}

.section-main img {
  width: 100%;
  height: auto;
}

.section-main-headline {
  text-transform: uppercase;
  letter-spacing: .025em;
  margin-bottom: 210px;
  font-family: Satoshi-Medium, sans-serif;
  font-size: 34px;
  line-height: 46px;
}

.section-main-description {
  color: #fff;
  margin-top: 200px;
  font-size: 22px;
  line-height: 26px;
}

.arrow-parent {
  position: relative;
}

.arrow {
  background-size: 100% 100%;
  position: absolute;
}

#down-arrow {
  width: 580px;
  height: 574px;
  background-image: url("downarrow.a730e69f.png");
  left: -250px;
  transform: translateY(-200vh);
}

#right-arrow {
  width: 612px;
  height: 300px;
  background-image: url("rightarrow.22acd84b.png");
  bottom: -65px;
  left: -700px;
}

#up-arrow {
  width: 195px;
  height: 522px;
  background-image: url("uparrow.79f98045.png");
  top: 110px;
  right: 12px;
}

.button-label {
  color: #224135;
  letter-spacing: .05em;
  font-size: 19px;
  text-decoration: underline;
  display: inline-block;
}

.button-download {
  text-align: center;
  text-transform: uppercase;
  height: auto;
  width: 193px;
  color: #224135;
  opacity: 1;
  letter-spacing: .05em;
  background-color: #0000;
  border: 1px solid #224135;
  border-radius: 30px;
  margin-left: 80px;
  padding: 6px 0 7px;
  font-size: 19px;
  line-height: 27px;
  text-decoration: none;
  transition: all .4s;
  display: inline-block;
}

.button-download:link, .button-download:active, .button-download:visited {
  color: #224135;
  text-decoration: none;
}

ul {
  padding: 0;
  list-style: none;
}

.footer-band {
  width: 100%;
}

.footer-top {
  padding: 110px 0;
  position: relative;
}

.footer-mid {
  z-index: 1;
  padding: 110px 0 130px;
  position: relative;
  overflow: hidden;
}

.footer-bottom {
  z-index: 2;
  padding: 60px 0;
  position: relative;
}

.footer-titles {
  margin-bottom: 111px;
}

.footer-title {
  text-align: right;
  color: #224135;
  letter-spacing: .025em;
  font-family: Satoshi-Medium, sans-serif;
  font-size: 36px;
  font-style: normal;
}

.footer-title a {
  border: 0;
}

.footer-detail {
  color: #224135;
  font-family: Satoshi-Regular, sans-serif;
  font-size: 20px;
  font-style: normal;
  line-height: 26px;
}

.footer-detail a:visited, .footer-detail a {
  color: #224135;
  text-decoration: none;
  transition: all .2s;
}

.footer-name {
  text-transform: uppercase;
  font-family: Satoshi-Medium, sans-serif;
  line-height: 32px;
}

.footer-credit {
  color: #fff;
  margin-bottom: 20px;
  font-family: Satoshi-Regular, sans-serif;
  font-size: 13px;
  font-style: normal;
  text-decoration: none;
}

.footer-credit a:visited, .footer-credit a {
  color: #fff;
  text-decoration: none;
  transition: all .2s;
}

.footer-icon-fox, .footer-icon-savills {
  height: 100px;
  width: auto;
  margin: 0 0 44px;
}

.footer-legals {
  color: #fff;
  font-size: 10px;
}

@media (max-width: 1199.98px) {
  .footer-top .row {
    text-align: center;
  }
}

@media (max-width: 991.98px) {
  .desktop {
    display: none;
  }

  .mobile {
    display: block;
  }

  .button-download {
    margin-left: 60px;
  }

  .logo-container {
    height: 94px;
  }

  .section-main-headline {
    text-align: center;
    margin: 40px 0;
  }

  .section-main-description {
    width: 80%;
    margin: 40px 0 0;
  }

  #right-arrow {
    width: 409px;
    height: 200px;
    background-image: url("rightarrow.22acd84b.png");
    bottom: -40px;
    left: -1200px;
  }

  .footer-icon-savills {
    margin-top: 44px;
  }

  .footer-title {
    text-align: left;
    margin-bottom: 60px;
  }

  .footer-top {
    padding: 80px 0;
  }

  .footer-mid {
    padding: 80px 0 100px;
  }

  .footer-bottom {
    padding: 40px 0 30px;
  }
}

@media (max-width: 767.98px) {
  .button-label {
    font-size: 16px;
  }

  .button-download {
    width: 160px;
    margin-left: 40px;
    font-size: 16px;
  }

  .footer-top {
    padding: 80px 0;
  }

  .logo-container {
    height: 80px;
    margin-top: 20px;
  }

  .intro-text-container {
    height: 120px;
    align-items: center;
    display: flex;
    position: absolute;
    top: auto;
    bottom: 0;
    left: 100px;
    transform: translate3d(0, 0%, 0);
  }

  .intro-text-container p {
    margin: 0;
    font-size: 18px;
    line-height: 24px;
  }

  .intro-image {
    width: 100%;
    height: calc(100% - 240px);
    background-position: 15%;
    position: absolute;
    top: 120px;
    right: 0;
  }

  #down-arrow {
    width: 320px;
    height: 317px;
    left: -200px;
  }

  .section-main {
    padding: 120px 0 100px;
  }

  #up-arrow, #right-arrow {
    display: none;
  }

  .content-intro {
    height: calc(100vh - 74px);
  }
}

@media (max-width: 575.98px) {
  .section-main-description {
    width: 100%;
    font-size: 18px;
    line-height: 23px;
  }

  .button-label {
    margin-bottom: 10px;
    font-size: 19px;
  }

  .button-download {
    width: 193px;
    margin: 15px auto 0;
    font-size: 19px;
    display: block;
  }

  .section-main-headline {
    font-size: 21px;
    line-height: 26px;
  }

  .intro-text-container p {
    font-size: 16px;
    line-height: 22px;
  }
}

@media (max-width: 389.99px) {
  .section-main-headline {
    font-size: 19px;
    line-height: 24px;
  }

  .intro-text-container p {
    font-size: 15px;
    line-height: 21px;
  }

  .small-mobile {
    display: block;
  }
}

@media (hover: hover) {
  .footer-credit a:hover, .footer-detail a:hover {
    color: #fdc5ac;
  }

  .button-download:hover {
    background-color: #fdc5ac66;
  }
}

